@import url(https://fonts.googleapis.com/css?family=Gloria+Hallelujah&display=swap);
h1 {
	font-family: 'Gloria Hallelujah', cursive;
	font-weight: 200;
	color: #0ccac4;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    background: rgb(19,6,245);
	background: -webkit-gradient(linear, left top, right top, from(rgba(19,6,245,1)), color-stop(60%, rgba(10,10,125,1)), to(rgba(43,0,92,1)));
	background: linear-gradient(90deg, rgba(19,6,245,1) 0%, rgba(10,10,125,1) 60%, rgba(43,0,92,1) 100%);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

